// アニメーションを動かす関数
const animationBtn = document.getElementById("startBtn");

function onclick() {
  document.querySelector(".animate-title").classList.toggle("inview");
  if (animationBtn.innerText === "セットする") {
    animationBtn.innerText = "スタート！";
  } else {
    animationBtn.innerText = "セットする";
  }
}

// ボタンの取得

// クリックしたときの処理（テキスト取得、分割して配列に入れる）
// テキストの取得
animationBtn.addEventListener("click", () => {
  const inputText = document.getElementById("inputText").value;

  // 取得したテキストを分割して配列にする
  let word = inputText.split("");
  console.log(word);
  for (let i = 0; i < inputText.length; i++) {
    const letter = document.getElementById("char" + i);
    letter.innerText = word[i];
    console.log(word[i]);
  }
  onclick();
});
